class App extends Component {
  constructor(props) {
    super(props);
    console.log(currentDay);
    this.state = {day: currentDay - 1, nextDay: currentDay, timeLeft: 0};
    this.period = 1;
    this.currentPerName = "1";
    this.contText = "";

    this.changeDay = this.changeDay.bind(this);
    this.calculateTimeLeft = this.calculateTimeLeft.bind(this);
  }

  get wednesday() {
    let fullDate = new Date().toString();
    let weekDay = fullDate.substr(0, 3);
    let returnValue = 0;
    if (weekDay === "Wed") {
      returnValue++;
    }
    return returnValue;
  }

  get wednesdayNext() {
    let fullDate = new Date().toString();
    let weekDay = fullDate.substr(0, 3);
    let returnValue = 0;
    if (weekDay === "Tue") {
      returnValue++;
    }
    return returnValue;
  }

  changeDay(e) {
    this.setState(state => ({
      day: e.value,
      nextDay: (e.value < 7) ? e.value + 1 : 0,
      timeLeft: state.timeLeft
    }));

    for (let i = 0; i < schedule.length; i++) {
      if (schedule[i][schedule[i].length - 1] === schedule[i][schedule[i].length - 2]) {
        schedule[i][schedule[i].length - 1].name += " Cont.";
      }
    }
  }

  calculateTimeLeft() {
    let fullDate = new Date().toString();
    let hourTime = Number(fullDate.substr(16, 2));
    let minuteTime = Number(fullDate.substr(19, 2));
    let currentTime = hourTime * 60 + minuteTime;

    let currentPeriodName = "";
    let timeLeft = 0;
    if (this.wednesday === 0) {
      for (let i = 0; i < notWedPeriodTimes.length; i++) {
        if (currentTime < notWedPeriodTimes[i]) {
          this.period = i;
          currentPeriodName = notWedPeriodNames[i];
          break;
        }
      }
      timeLeft = notWedPeriodTimes[this.period] - currentTime;
    } else {
      for (let i = 0; i < wedPeriodTimes.length; i++) {
        if (currentTime < wedPeriodTimes[i]) {
          this.period = i;
          currentPeriodName = wedPeriodNames[i];
          break;
        }
      }
      timeLeft = wedPeriodTimes[this.period] - currentTime;
    }
    this.currentPerName = currentPeriodName;

    if (currentPeriodName === "") {
      this.currentPerName = "6";
    }

    this.setState(state => ({
      day: this.state.day,
      nextDay: this.state.nextDay,
      timeLeft: timeLeft
    }));
  }

  componentDidMount() {
    this.interval = setInterval(() => this.calculateTimeLeft(), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }
  
  render() {
    return (
      <div className="App">
        <header className="dayHeader">
          <label htmlFor="daySelector" className="dayLabel">Day: </label>
          <Select name="days" className="daySelector" value={days.find(nDay => nDay.value === this.state.day)} options={days} onChange={this.changeDay}/>
        </header>
        
        <div className="fullDay">
          <h1>{"Day " + (this.state.day + 1)}</h1>
          <ul>
            {schedule[this.state.day].map(period => (
              <div className="listDiv">
                <li className="list">{period.name + " @ " + period.rooms[this.state.day]}</li>
                <li className="listTime">{times[this.wednesday][schedule[this.state.day].indexOf(period)][0] + " - " + times[this.wednesday][schedule[this.state.day].indexOf(period)][1]}</li>
              </div>            
            ))}
          </ul>
        </div>

        <div className="nextCurrent">
          <h2>{ (this.currentPerName === "advisory" || this.currentPerName === "lunch") ? this.state.timeLeft + " minutes left in " + this.currentPerName : this.state.timeLeft + " minutes left in " + schedule[this.state.day][Number(this.currentPerName) - 1].name}</h2>
        </div>

        <div className="tmrw">
          <h1>{"Day " + (this.state.nextDay + 1)}</h1>
          <ul>
            {schedule[this.state.nextDay].map(period => (
              <div className="listDiv">
                <li className="list">{period.name + " @ " + period.rooms[this.state.nextDay]}</li>
                <li className="listTime">{times[this.wednesdayNext][schedule[this.state.nextDay].indexOf(period)][0] + " - " + times[this.wednesdayNext][schedule[this.state.nextDay].indexOf(period)][1]}</li>
              </div>
              ))}
          </ul>
        </div>
      </div>
    );
  }
}


export default App;
